.App {
  @import "../node_modules//bootstrap/scss//bootstrap.scss";

  width: 100%;
  height: max-content;
  min-height: 100%;
  transition: background-color 0.2s ease-in;
  // background-color: #020333;
  // background-color: #090a2b;
  background-color: #1D1D27;
  color: #d8d4d4;

  &[theme-mode="light"] {
    background: white;
    color: black;
  }

  padding-bottom: 20%;

  .no_color {
    color: inherit;
    &:hover{
      color: inherit;
    }
    &:focus{
      color: inherit;
    }
  }

}